.learning {

  &__title {
    margin-bottom: 30px;
    text-align: center;
    max-width: 679px;
    font: $h2;
  }

  &__subtitle {
    text-align: center;
    max-width: 846px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item:not(:last-child) {
    margin-right: 123px;
  }
  
}

.underline {
  position: relative;

  &::after {
    content:'';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 101%;
    height: 7px;
    border-radius: 5px;
    background: linear-gradient(264.91deg, #7C4EAB 0.73%, #DD51D4 95.85%);;
  }
}

.color-number {
  color: $color-purple;
}

.learning-block {
  margin-bottom: 30px;
  max-width: 290px;

  &__img {
    margin-bottom: 30px;
    padding: 11px;
    max-width: 70px;
    max-height: 70px;
    border-radius: 15px;
    background-color: $color-purple;
  }

  &__title {
    margin-bottom: 15px;
    font: $h5;
  }

  &__descr {
    font-size: 18px;
    line-height: 28px;
  }
}

.learning-svg {
  fill: #fff;
}

@media (max-width:1280px) {

  .learning {

    &__title {
      font-size: 40px;
    }

    &__subtitle {
      max-width: 684px;
    }
  }

  .underline::after {
    bottom: -18px;
  }

  .learning-block {
    max-width: 226px;

    &:not(:last-child) {
      margin-right: 88px;
    }

    &__img {
      margin-bottom: 20px;
      width: 60px;
      height: 60px;

      svg {
        width: 38px;
        height: 38px;
      }
    }

    &__title { 
      margin-bottom: 10px;
      font-size: 20px;
      line-height: 30px;
    }

    &__descr {
      font-size: 16px;
      line-height: 25px;
    }
  }
}

@media (max-width:768px) {
  
  .learning {
    &__title {
      margin-bottom: 25px;
    }

    &__list {
      padding: 0 84px;
      justify-content: space-between;
    }
  }

  .learning-block {
    max-width: 255px;
    margin-bottom: 60px;

    &:not(:last-child) {
      margin-right: 0;
    }
  }

  .underline::after {
    bottom: -12px;
  }
}

@media (max-width:500px) {
  
  .learning {
    &__title {
      font: $h5;
    }

    &__list {
      justify-content: center;
      padding: 0 35px;
    }
  }

  .underline::after {
    bottom: -10px;
  }

  .learning-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 0;
    max-width: 260px;

    &:not(:last-child) {
      margin-bottom: 44px;
    }

    &__title {
      font-size: 18px;
      line-height: 27px;
    }
  }
}