.back-left {
  position: absolute;
  top: -198px;
  left: -681px;
  width: 783px;
  height: 783px;
  background: url(/img/back-left.svg) no-repeat center center;
}

.back-right {
  position: absolute;
  top: 384.51px;
  right: -722px;
  width: 759px;
  height: 759px;
  background: url(/img/back-right.svg) no-repeat center center;
}

.header {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 35px 195px;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__logo {
    position: relative;
    margin-right: 23px;
    padding-left: 83px;
    max-width: 241px;
    max-height: 71px;

    &::before {
      content: '';
      position: absolute;
      top: -39px;
      left: 0;
      width: 61px;
      height: 31px;
      background: url(/img/logo4.svg) no-repeat top left;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 13px 0 13px 25px;
    border-left: 1px solid #998d97;
  }

  &__descr {
    display: inline-block;
    margin-right: 44px;
    max-width: 189px;
    font-family: $Lato;
    font-size: 14px;
    line-height: 18px;
  }

  &__right {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

.choices {
  margin-right: 50px;
  margin-bottom: 0;
  min-width: 150px;
  max-height: 36px;

  &__inner {
    padding: 0;
    padding-right: 24px;
    min-height: unset;
    font: $h5;
    vertical-align: unset;
    border: transparent;
    background-color: transparent;
  }

  &__list {
    padding: 0;
  }
}

.is-open .choices__list--dropdown, .is-open .choices__list[aria-expanded] {
  border-color: #998d97;
  border-top: transparent;
}

.choices[data-type*=select-one]::after {
  top: 46%;
  right: 5px;
  margin-top: 0;
  border-top-width: 8px;
  border-left-width: 6px;
  border-right-width: 6px;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
  font-family: $Lato;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted::after {
  content: '';
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right: 10px;
}

.header__nav-link {
  font: $h5;
}

.header__phone {
  display: inline-flex;
  align-items: center;
  font: $h5;
}

.phone {
  margin-right: 8px;
  fill: $color-main;
}

.btn-login {
  display: inline-flex;
  align-items: center;
  margin-left: 91px;
  padding: 5px 31px;
  font-family: "Lato";
  font-size: 23px;
  font-weight: 500;
  line-height: 35px;
  letter-spacing: 1px;
  background-color: $color-purple;
  border-radius: 12px;
  color: #fff;

  &__arrow {
    margin-right: 14px;
    fill: #fff;
  }
}

.burger {
  display: none;
  cursor: pointer;

  &__line {
    transition: background-color 0.2s ease-in-out;
  }
}

@media (max-width:1280px) {
  .back-left {
    top: -110px;
    left: -423px;
    height: 446px;
    width: 446px;
    background-size: 446px;
  }

  .back-right {
    display: none;
  }

  .header {

    &__container {
      padding: 30px 40px;
    }

    &__logo {
      align-self: flex-start;
      margin-right: 15px;
      padding-left: 61px;

      &::before {
        top: -14px;
        width: 45px;
        height: 23px;
        background-size: 45px;
      }
    }

    &__nav {
      padding: 12px 0 12px 18px;
    }

    &__descr {
      margin-right: 52px;
      max-width: 135px;
      font-size: 10px;
      line-height: 13px;
    }

    &__nav-link {
      font: $h5_1280;
    }

    &__phone {
      font: $h5_1280;
    }
  }

  .logo-img {
    max-width: 126px;
  }

  .choices {
    margin-right: 36px;
    min-width: 120px;

    &__inner {
      padding-right: 0;
    font: $h5_1280;
    }
  }

  .phone {
    width: 20px;
    height: 20px;
  }

  .btn-login {
    font-size: 18px;

    &__arrow {
      width: 20px;
      height: 20px;
    }
  }
}

@media (max-width:768px) {

  .back-left {
    display: none;
  }

  .header {

    &__container {
      padding: 35px 15px;
    }

    &__left {
      flex-wrap: wrap;
    }

    &__logo {
      align-self: center;
    }

    &__descr {
      margin-right: 15px;
      max-width: 133px;
    }

    &__nav-link {
      display: none;  
    }

    &__right {
      align-items: center;
      padding-top: 7px;
    }

    &__phone {
      display: none;
    }
  }

  .choices {
    display: none;
  }
  
  .btn-login {
    margin-bottom: 10px;
    margin-left: 0;
    padding: 5px 34px;
  }

  .burger {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 10px;
    margin-left: 80px;
    width: 42px;
    height: 28px;

    &__line {
      width: 100%;
      height: 4px;
      border-radius: 4px;
      background-color: $color-purple;
      
      &:last-child {
        width: 79%;
      }
    }
  }
}

@media (max-width:500px) {

  .header {
    
    &__nav {
      display: none;
    }

    &__logo {
      padding-left: 45px;

      &::before {
        top: -27px;
        width: 35px;
        height: 18px;
        background-size: 35px;
      }
    }

    &__right {
      flex-wrap: nowrap;
      padding-top: 0;
    }
  }

  .logo-img {
    max-width: 94px;
  }

  .btn-login {
    margin-bottom: 0;
    padding: 4px 20px;
    border-radius: 6px;
    font-size: 14px;
    line-height: 28px;

    svg {
      margin-right: 6px;
      width: 13px;
      height: 13px;
    }
  }

  .burger {
    margin-bottom: 0;
    margin-left: 21px;
  }
}
