.history {
  margin-bottom: 133px;

  &__title {
    text-align: center;
    align-self: center;
    max-width: 844px;
    font: $h2;
  }

  &__pic {
    display: flex;
    justify-content: space-between;
  } 

  &__content {
    padding-left: 117px;
    padding-right: 92px;
  }

  &__pic--bottom {
    padding-left: 17px;
  }

  &__descr--right {
    padding-top: 115px;
    padding-right: 44px;
    max-width: 59.3%;
  }

  &__text {
    padding-top: 106px;
    max-width: 58%;
  }

  &__img-tablet {
    width: 100%;
    min-width: 270px;
    min-height: 277px;
  }

  &__img-statistic {
    width: 100%;
    min-width: 270px;
    min-height: 270px;
  }

}

.picture-left {
  margin-right: 30px;
}

.picture-right {
  margin-left: 30px;
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 85px;
}

@media (max-width:1280px) {
  
  .history {
    margin-bottom: 137px;

    &__title {
      max-width: 652px;
      font-size: 40px;
    }

    &__content {
      padding-left: 103px;
    }

    &__img-tablet {
      max-width: 350px;
    }

    &__descr--right {
      padding-top: 84px;
      padding-right: 10px;
      max-width: 57.3%;
    }

    &__pic--bottom {
      padding-left: 0;
    }

    &__text {
      padding-top: 57px;
    }

    &__img-statistic {
      max-width: 355px;
    }
  }
}

@media (max-width:768px) {
  
  .history {
    margin-bottom: 120px;

    &__content {
      padding: 0;
    }

    &__img-tablet {
      max-width: 270px;
    }

    &__img-statistic {
      max-width: 270px;
    }

    &__descr--right {
      padding-top: 5px;
      padding-right: 0px;
      max-width: 58%;
    }

    &__text {
      padding-top: 50px;
    }
  }
}

@media (max-width:500px) {
  .history {
    margin-bottom: 100px;

    &__title {
      font: $h5;
    }

    &__pic--top {
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
    }

    &__pic--bottom {
      flex-direction: column-reverse;
      align-items: center;
    }

    &__descr--right {
      max-width: 100%;
    }

    &__text {
      padding-top: 25px;
      max-width: 100%;
    }
  }
}

.picture-left {
  margin-right: 0;
  margin-bottom: 21px;
}

.picture-right {
  margin-left: 0;
}


.title {
  margin-bottom: 50px;
}