.header {

  &__nav-link {
    position: relative;
    transition: color 0.2s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -5px;
      left: 0;
      margin: auto;
      width: 0%;
      height: 1px;
      background-color: $color-main;
      transition: width 0.2s ease-in-out;
    }

    &:hover {
      color: $color-purple;
      transition: color 0.2s ease-in-out;
    }

    &:hover::after {
      width: 100%;
      transition: width 0.2s ease-in-out;
    }
  }

  &__phone {
    transition: color 0.2s ease-in-out;
  }

  &__phone:hover {
    color: $color-purple;
    transition: color 0.2s ease-in-out;

    .phone {
      animation: wiggle .08s alternate ease infinite;
    }
  } 
  
}

@keyframes wiggle {
	from {transform: rotate(0deg); }
	to {transform: rotate(30deg); }
}

.choices__inner {
  transition: color 0.2s ease-in-out;
  
  &:hover {
    color: $color-purple;
    transition: color 0.2s ease-in-out;
  }
}

.btn-login {
  position: relative;
  transition: background-color 0.6s cubic-bezier(0.000, 0.000, 0.580, 1.000);

  span {
    z-index: 10;
  }

  svg {
    z-index: 10;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    mix-blend-mode: darken;
    border-radius: 8px;
    background-color: #7C4EAB;
    opacity: 0.7;
    transition: width 0.5s cubic-bezier(0.000, 0.000, 0.580, 1.000), opacity 0.4s ease-in-out;
  }

  &:hover {
    background-color: transparent;
    color: #fff;
    transition: background-color 0.6s cubic-bezier(0.000, 0.000, 0.580, 1.000);
  }

  &:hover::after {
    width: 100%;
    opacity: 1;
    transition: width 0.5s cubic-bezier(0.000, 0.000, 0.580, 1.000), opacity 0.4s ease-in-out;
  }
}


.burger:hover .burger__line {
  background-color: #7C4EAB;
  transition: background-color 0.2s ease-in-out;
}