.hero {
  margin-bottom: 145px;
  padding-top: 31px;

  &__nav-list {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }

  &__nav-item:not(:last-child) {
    margin-right: 8px;
  }

  &__nav-link {
    font-family: $Lato;
    font-size: 16px;
    line-height: 19px;
    opacity: 0.5;
  }

  &__content {
    display:flex;
    
  }

  &__left {
    max-width: 57%;
  }

  &__right {
    position: relative;
    background: url(/img/stars.svg) no-repeat;
    background-position-y: bottom;
    background-position-x: 42px;
    
  }

  &__title {
    margin-bottom: 22px;
    font: $h1;
  }

  &__price {
    display: inline-flex;
    align-items: center;
    margin-bottom: 16px;
  }

  &__pic {
    width: 100%;
    height: 100%;
  }

  &__img {
    position: relative;
    left: 70px;
  }
}

.text-color {
  color: $color-purple;
}

.text {
  font: $text;
  opacity: 0.8;
}

.text-gap {
  margin-bottom: 15px;
}

.nav-stroke {
  display: inline-block;
  margin-right: 8px;
  width: 10px;
  height: 1px;
  background-color: $color-main;
  opacity: 0.5;
}

.spent {
  margin-right: 25px;
  fill: $color-purple;
}

.price-block {
  &__text {
    font-family: $Lato;
    font-size: 29px;
    line-height: 29px;

    &--1 {
      margin-right: 5px;
    }
  }

  &__value {
    margin-right: 5px;
    font-family: $Lato;
    font-size: 38px;
    font-weight: 700;
    line-height: 55px;
    color: $color-purple;
  }
}

@media (max-width:1280px) {

  .hero {
    margin-bottom: 77px;
    padding-top: 34px;

    &__left {
      max-width: 60%;
    }

    &__title {
      margin-bottom: 40px;
      font: $h1_1280;
    }

    &__price {
      margin-bottom: 40px;
    }

    &__right {
      max-width: 40%;
      background-position-y: 42px;
      background-position-x: 21px;
      background-size: 458px;
    }

    &__img {
      left: 45px;
    }
  }

  .text {
    font: $text_1280;
  }
}

@media (max-width:768px) {
  
  .hero {
    margin-bottom: 65px;
    padding-top: 10px;
    background-image: url(/img/superman_768.webp);
    background-repeat: no-repeat;
    background-position-x: 147%;
    background-position-y: 72%;

    &__nav-list {
      padding-left: 10px;
    }

    &__left {
      max-width: 100%;
    }

    &__title {
      margin-bottom: 17px;
    }

    &__price {
      margin-bottom: 17px;
    }

    &__text {
      padding-left: 5px;
      max-width: 60%;

    }

    &__right {
      display: none;
    }
  }
}

@media (max-width:500px) {
  
  .hero {
    margin-bottom: 85px;
    padding-top: 5px;
    background-position-x: 198%;
    background-position-y: 5%;
    background-size: 245px;

    &__nav-list {
      margin-bottom: 44px;
      padding-left: 0;
    }

    &__title {
      margin-bottom: 23px;
      width: 69%;
      font: $h1_360;
    }

    &__price {
      margin-bottom: 14px;
    }

    &__text {
      padding-left: 0;
      max-width: 100%;
    }
  }

  .text {
    font-size: 18px;
    line-height: 29px;
  }

  .text-gap {
    margin-bottom: 11px;
  }
}