html {
  box-sizing: border-box;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


.body {
  margin: 0;
  font-family: sans-serif;
  font-size: 16px;
  color: #331A2F;
}

a {
  text-decoration: none;
  color: inherit;
}


h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
  padding: 0;
}

h4 {
  margin: 0;
  padding: 0;
}

h5 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 195px;
  max-width: 1920px;
}

.main-container {
  position: relative;
  margin: 0 auto;
  overflow-x: hidden;
}

@media (max-width:1280px) {

  .container {
    padding: 0 40px;
    max-width: 1280px;
  }
}

@media (max-width:768px) {

  .container {
    padding: 0 15px;
    max-width: 768px;
  }
}

@media (max-width:500px) {

  .container {
    max-width: 500px;
  }
}